<template>
    <div class="slider-container">
        <AppProductCard />
        <AppProductCard />
        <AppProductCard />
        <AppProductCard />
        <AppProductCard />
        <AppProductCard />
    </div>
</template>

<script>
import { onMounted, defineAsyncComponent } from 'vue'
import Flickity from 'flickity'
import 'flickity/dist/flickity.min.css'

export default {
    components: {
        AppProductCard: defineAsyncComponent(() => import('@/components/AppProductCard'))
    },
    setup() {
        onMounted(() => {
            require('flickity-imagesloaded');
            const el = document.querySelector('.slider-container');
            new Flickity( el, {
                contain: true,
                groupCells: true,
                pageDots: false,
                draggable: false,
                cellAlign: 'left',
                imagesLoaded: true
            })
        })
    }
}
</script>

